import React, { useEffect, useState } from "react";
import { withRouter } from "react-router-dom";
import axios from "axios";
import "./Participants.css";
import { variables } from "../../variables";
import { CSVLink } from "react-csv";
// import ReactHTMLTableToExcel from "react-html-table-to-excel";

function Participants(props) {
  const [participantData, setParticipantData] = useState([[]]);
  const [eA, setEA] = useState([[]]);
  useEffect(() => {
    // console.log(props.match.params.eventName);
    axios
      .get(
        `${variables.backendURL}/api/teams/${props.match.params.eventName}/${variables.protectedToken}`
      )
      .then((res) => {
        // console.log(res.data);
        if (res.data[0]) {
          setParticipantData(res.data);
          let emailArr = [["Team No", "Name", "Email", "Contact", "College"]];
          res.data.map((participants, i) => {
            return participants.map((team, j) => {
              return emailArr.push([
                i + 1,
                team.name,
                team.email,
                team.contact,
                team.college,
              ]);
            });
          });
          setEA(emailArr);
          // console.log(emailArr);
        } else {
          setParticipantData([[]]);
        }
      });
  }, [props.match.params.eventName]);

  return (
    <div className="participants-div">
      <p>
        {props.match.params.eventName.charAt(0).toUpperCase() +
          props.match.params.eventName.slice(1)}{" "}
        Participants
      </p>

      <CSVLink
        data={eA}
        filename={`${props.match.params.eventName}Data.csv`}
        className="csv-link-ind"
      >
        Download {`${props.match.params.eventName}`} Data
      </CSVLink>

      {/* <ReactHTMLTableToExcel
        id="export-to-excel-btn"
        className="button-excel download-table-xls-button"
        table="participants-table"
        filename="allusers"
        sheet="sheet 1"
        buttonText="Download as XLS"
      /> */}
      <div className="container table-responsive">
        <table className="table table-bordered table-dark table-hover participants-table">
          <thead>
            <tr>
              <th scope="col">Team No.</th>
              <th scope="col">Name</th>
              <th scope="col">Email</th>
              <th scope="col">Contact</th>
              <th scope="col">College</th>
              {/* <th scope="col">UID</th> */}
            </tr>
          </thead>
          <tbody>
            {participantData.map((team, i) => {
              return (
                <React.Fragment key={i}>
                  <tr>
                    <th scope="row" rowSpan={team.length + 1}>
                      {i + 1}
                    </th>
                  </tr>
                  {team.map((each, j) => {
                    return (
                      <tr key={j}>
                        <td>{each.name}</td>
                        <td>{each.email}</td>
                        <td>{each.contact}</td>
                        <td>{each.college}</td>
                        {/* <td>{each.uid}</td> */}
                      </tr>
                    );
                  })}
                </React.Fragment>
              );
            })}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default withRouter(Participants);
